import React from "react";
import {Group, PanelHeader, PanelHeaderBack, Placeholder, RichCell, Avatar, Button, Select, Div} from "@vkontakte/vkui";
import NotifSnack from "./NotifSnack";
import Icon24Flash from '@vkontakte/icons/dist/24/flash';
import Icon24Error from '@vkontakte/icons/dist/24/error';
import getParams from "../functions/getParams";
import getRand from "../functions/getRand";
import usersUpdate from "../functions/usersUpdate";
import areasUpdate from "../functions/areasUpdate";
import check0100 from "../functions/check0100";
import Icon56RecentOutline from '@vkontakte/icons/dist/56/recent_outline';
import Icon28HelpOutline from '@vkontakte/icons/dist/28/help_outline';

class LawsPanel extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            snackbar: '',
            constitution: this.props.user.constitution
        }
        this.diversion = this.diversion.bind(this);
        this.closeSnackbar = this.closeSnackbar.bind(this);
        this.openSnackbar = this.openSnackbar.bind(this);
        this.updateSelect = this.updateSelect.bind(this);
    }
    x = [
        {
            icon: 'https://c7.hotpng.com/preview/971/481/210/5bbc337a03e9e.jpg',
            name: 'Вооруженные силы',
            options: [
                'Мирное государство',
                'Народное ополчение',
                'Регулярная армия'
            ],
            help: [
                'вы не имеете армии, на территориях не может быть выставлен гарнизон, нет расходов на армию',
                'армия состоит из мужчин среднего возраста, расходы на армию - 25%, сила армии - 50%',
                'армия состоит из специально обученных мужчин среднего возраста, расходы на армию - 100%, сила армии - 100%'
            ]
        },
        {
            icon: 'https://c7.hotpng.com/preview/822/646/427/export-import-international-trade-clip-art-others.jpg',
            name: 'Торговля',
            options: [
                'Протекционизм',
                'Повышенные пошлины',
                'Свобода торговли'
            ],
            help: [
                'стоимость импорта увеличивается на 50%, скорость производства увеличивается на 50%',
                'стоимость импорта увеличивается на 25%, скорость производства увеличивается на 30%',
                'нет эффектов'
            ]
        },
        {
            icon: 'https://lh3.googleusercontent.com/proxy/PJmJlb2SAlf7pTdPeFd9FkFf80jwqd7LbxyrRsvW6RiXz95tv0qUgCfmwbjgiGhFS7ac-wEwYwIVI5QSNOMPI9k9',
            name: 'Экология',
            options: [
                'Производство важнее',
                'Стандартно',
                'Природа важнее'
            ],
            help: [
                'скорость производства увеличивается на 100%, каждая ед. продукции уменьшает счастье на 0.1',
                'нет эффектов',
                'скорость производства уменьшается на 50%, каждая ед. продукции увеличивает счастье на 0.1'
            ]
        },
        {
            icon: 'https://img2.freepng.ru/20180329/cqw/kisspng-raised-fist-clip-art-power-5abc716713ac21.1149513415222992390806.jpg',
            name: 'Власть',
            options: [
                'Единоличная',
                'Разделение "на бумаге"',
                'Настоящее разделение'
            ],
            help: [
                'потерять власть можно только в случае захвата',
                '20% экспорта переводится в швейцарский банк',
                'x2 очков поддержки власти за любую речь'
            ]
        },
        {
            icon: 'https://lh3.googleusercontent.com/proxy/2G_i1bWqzY5vVSbhZbS6ZngxffiX7CVuOM20ku5MK7E8cnMlQ0NuCfc1zUj--OMXv3NIKACVfa_aAz05mNaVUI662Hi-I347TpIW2nNYU_z-DLP2k4jr0fd9RA',
            name: 'Права и свободы',
            options: [
                'Тотальный контроль',
                'Контроль для безопасности',
                'Полная свобода'
            ],
            help: [
                'макс. уровень счастья - 50, шанс удачной диверсии: 25%',
                'нет эффектов',
                'x2 счастья за речь, шанс удачной диверсии: 100%'
            ]
        },
        {
            icon: 'https://w7.pngwing.com/pngs/967/952/png-transparent-finance-investor-money-business-business-people-investment-business.png',
            name: 'Занятость',
            options: [
                'Детский труд',
                'Уник? Лучше на завод',
                'Счастливая пенсия'
            ],
            help: [
                'скорость производства +50%, -0.1 счастья за ед. продукции',
                'скорость производства +20%, запрет производства очков науки',
                'скорость производства -10%, +0.05 счастья за ед.продукции'
            ]
        }
    ];
    closeSnackbar(){
        this.setState({ snackbar: null })
    }
    openSnackbar(g,m){
        this.setState({
            snackbar: <NotifSnack closeSnackbar={this.closeSnackbar} good={g} message={m}/>
        });
    }
    diversion(e){
        let q = this.props.areas;
        let w = q[this.props.curArea];

        w.approval = check0100(w.approval);
        w.happiness = check0100(w.happiness);

        areasUpdate(q);
    }
    openHelp(){

    }
    updateSelect(e,i){
        let x = this.state.constitution;
        x[i] = parseInt(e.target.value);
        this.setState({
            constitution: x
        });
    }
    render() {
        let h = (new Date()).getHours();
        const header = <PanelHeader
            left={<PanelHeaderBack onClick={() => {this.props.openPanel('main')}}/>}
        >
            Законы
        </PanelHeader>;
        if(h >= 0 && h < 25){
            return (
                <div>
                    {header}
                    <Group>
                        {this.x.map((v,i) =>
                            <RichCell
                                disabled
                                multiline
                                before={<Avatar size={72} src={v.icon} />}
                                after={<Icon28HelpOutline/>}
                                actions={
                                    <Select onChange={q => {this.updateSelect(q,i)}} value={this.state.constitution[i]}>
                                        {v.options.map((z,b) => <option value={b}>{z}</option>)}
                                    </Select>
                                }
                            >
                                {v.name}
                            </RichCell>
                        )}
                        <Div>
                            <Button size="xl">
                                Принять конституцию
                            </Button>
                        </Div>
                    </Group>
                    {this.state.snackbar}
                </div>
            )
        }
        else{
            return <div>
                {header}
                <Placeholder
                    icon={<Icon56RecentOutline/>}
                    header="Закрыто"
                >
                    Принимать законы можно с 12 до 13 часов
                </Placeholder>
            </div>
        }
    }
}

export default LawsPanel;