import React from "react";
import {
    Button, Div,
    Group,
    Header, Input,
    PanelHeader,
    PanelHeaderBack, Select,
    SimpleCell,
    Slider
} from "@vkontakte/vkui";
import NotifSnack from "./NotifSnack";
import areasUpdate from "../functions/areasUpdate";
import Icon28CheckCircleOutline from '@vkontakte/icons/dist/28/check_circle_outline';
import Icon32Spinner from '@vkontakte/icons/dist/32/spinner';
import economy from "../const/economy";
import getUnix from "../functions/getUnix";
import timeFormat from "../functions/timeFormat";
import getParams from "../functions/getParams";
import usersUpdate from "../functions/usersUpdate";

class TavernPanel extends React.Component{
    diversionOffset = 60*30;
    constructor(props) {
        super(props);
        this.state = {
            snackbar: '',
            sliderVal: 1,
            selectVal: 0
        }
        this.pushProduction = this.pushProduction.bind(this);
        this.closeSnackbar = this.closeSnackbar.bind(this);
        this.openSnackbar = this.openSnackbar.bind(this);
        this.updateSelect = this.updateSelect.bind(this);
    }
    closeSnackbar(){
        this.setState({ snackbar: null })
    }
    openSnackbar(g,m){
        this.setState({
            snackbar: <NotifSnack closeSnackbar={this.closeSnackbar} good={g} message={m}/>
        });
    }
    pushProduction(){
        let prodObj = {
            count: this.state.sliderVal,
            ready: getUnix() + this.state.sliderVal
        }

        let q = this.props.areas;
        let w = q[this.props.curArea];
        let z = this.props.users;
        let x = this.props.user;

        if(x.warehouse[0] - this.state.sliderVal*10 < 0){
            this.openSnackbar(false, 'Недостаточно пивка');
            return;
        }

        if(w.training.length >= 1){
            this.openSnackbar(false, 'Состав уже обучается');
            return;
        }

        x.warehouse[0] -= this.state.sliderVal*10;
        w.training.push(prodObj);
        q[this.props.curArea] = w;
        z['U'+getParams('vk_user_id')] = x;
        areasUpdate(q);
        usersUpdate(z);
    }
    collectProd(i){
        let q = this.props.areas;
        let w = this.props.areas[this.props.curArea];
        let z = this.props.users;
        let x = this.props.user;
        this.openSnackbar(true, 'Челики записаны в состав');
        x.army.count += w.training[i].count;
        z['U'+getParams('vk_user_id')] = x;
        q[this.props.curArea].training.splice(i,1);
        areasUpdate(q);
        usersUpdate(z);
    }
    updateSelect(e){
        this.setState({
            selectVal: e.target.value
        });
    }
    render() {
        return (
            <div>
                <PanelHeader
                    left={<PanelHeaderBack onClick={() => {this.props.openPanel('area')}}/>}
                >
                    Пивнуха
                </PanelHeader>
                <Group description="тренируйте челиков, чтобы отправлять их на разборки в другие районы">
                    <Div>
                        <Slider
                            step={1}
                            min={1}
                            max={100}
                            value={Number(this.state.sliderVal)}
                            onChange={v => {this.setState({sliderVal: v})}}
                            top=""
                        />
                        <Input style={{margin: '15px 0'}} value={this.state.sliderVal} disabled/>
                        <Header mode="secondary">понадобится пивка: </Header>
                        <Input style={{margin: '15px 0'}} value={this.state.sliderVal*10} disabled/>
                        <Button onClick={this.pushProduction} style={{marginTop: 15}} size="xl">Тренировать</Button>
                    </Div>
                </Group>
                <Group header={<Header mode="secondary">Тренировка</Header>}>
                    {this.props.areas[this.props.curArea].training.map((v, i) => {
                        let before, description, click;
                        if(getUnix() - v.ready <= 0){
                            before = <Icon32Spinner width={28} height={28}/>;
                            let date = new Date(v.ready*1000);
                            let minutes = timeFormat(date.getMinutes());
                            let hours = timeFormat(date.getHours());
                            description = `будут готовы в ${hours}:${minutes}`;
                            click = () => {return false;};
                        }
                        else{
                            before = <Icon28CheckCircleOutline/>;
                            description = 'нажмите, чтобы записать в штат';
                            click = () => {this.collectProd(i)}
                        }
                        return (
                            <SimpleCell onClick={click} key={v.ready+description} description={description} before={before}>
                                {`Челики (${v.count})`}
                            </SimpleCell>
                        )
                    })}
                </Group>
                {this.state.snackbar}
            </div>
        )
    }
}

export default TavernPanel;